    border-bottom: 3px solid var(--Yellow);
    color: var(--Black);
    font-weight: bold;
    padding: 0px 5px 5px 5px;
} */
.top-navbar-section {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 17px 0px;
}

.top-navbar-left,
.top-navbar-right,
.search_box {
    display: flex;
    justify-content: center;
    align-items: center;
}

.fa-bell,
.fa-clock,
.fa-long-arrow-alt-left {
    font-size: 25px;
    padding: 10px 15px;
}

.fa-long-arrow-alt-left {
    padding: 15px;
    background-color: rgb(240, 240, 240);
    border-radius: 17px;
}

.notification span {
    padding: 4px 7px;
    border-radius: 50%;
    background-color: var(--Yellow);
    position: absolute;
    top: 1.5rem;
    font-size: 12px;
}

.fa-chevron-right {
    font-size: 15px;
    padding: 10px 15px;
}

.dash {
    color: rgb(184, 184, 184);
    padding: 0px 20px;
}

.sales {
    font-weight: bold;
}


/* Search */

.search-box {
    margin: 0px 0px 0px 15px;
    padding: 10px;
    height: 20px;
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(240, 240, 240);
    border-radius: 10px;
    font-size: 18px;
}

.search-box input {
    border: none;
    outline: none;
    background-color: rgb(240, 240, 240);
    padding: 0px 10px;
}
.order-customer-section {
    width: 24%;
}

.order-customer-order,
.order-customer-customer {
    margin: 10px 10px 10px 10px;
    padding: 22px;
    box-shadow: 0 0.8rem 1.8rem hsl(0 0% 0% / 20%);
    border-radius: 35px;
    /* height: 101px; */
}

.order-customer-customer {
    margin-top: 20px;
}

.order-customer-order-name-details,
.order-customer-customer-name-details {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

.fa-calendar-check,
.fa-user-plus {
    padding: 5px 5px;
    background-color: var(--Yellow);
    border-radius: 5px;
    height: 10px;
    font-size: 12px;
    margin: 0px 5px 0px 0px;
    color: var(--Black);
}

.fa-calendar-check {
    background-color: var(--Red);
    color: var(--White);
}

.order-customer-order-name-details h4,
p,
.order-customer-customer-name-details h4,
p {
    margin: 0px;
    padding: 0px 0px 0px 0px;
}

.order-customer-order-name-details h4,
.order-customer-customer-name-details h4 {
    font-size: 12px;
    font-weight: bold;
}

.order-customer-order-name-details p,
.order-customer-customer-name-details p {
    font-size: 12px;
    font-weight: bold;
    color: var(--Red);
}

.order-customer-customer-name-details p {
    color: rgb(15, 209, 15);
}

.percent {
    padding: 0px 0px 0px 30px;
}

.order-customer-order h3,
.order-customer-customer h3 {
    text-align: center;
    font-weight: bold;
    font-size: 30px;
    padding: 10px 0px 0px 0px;
    border-bottom: 2px solid #ccc;
}
.sales-chart-section {
    margin: 10px 0px 0px 0px;
    width: 45%;
    padding: 25px;
    box-shadow: 0 0.8rem 1.8rem hsl(0 0% 0% / 20%);
    border-radius: 35px;
}

.sales-chart-section h3 {
    font-size: 16px;
    font-weight: bolder;
}
.sidebar-section {
    border-right: 1px solid #ccc;
    height: 100vh;
    position: fixed;
    padding: 20px 0px;
}

.sidebar-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    color: var(--Red);
}

.sidebar-logo i {
    padding: 10px;
    background-color: rgb(240, 240, 240);
    border-radius: 17px;
}

.sidebar-logo h3 {
    padding: 0px 0px 0px 30px;
    letter-spacing: 1px;
    line-height: 2rem;
    font-weight: bold;
}

.sidebar-logo h3 span {
    color: var(--Black);
}

.sidebar-section ul {
    list-style: none;
}

.sidebar-links {
    color: var(--Black);
    font-weight: bold;
    text-decoration: none;
    font-size: 16px;
}

.sidebar-links i {
    padding: 0px 20px 0px 0px;
    color: var(--Gray);
}

.sidebar-items {
    padding: 15px 0px 15px 15px;
    margin: 0px 20px 0px 0px;
}

.sidebar-items:hover {
    background-color: var(--Yellow);
    border-radius: 15px;
    cursor: pointer;
}

.other {
    font-size: 15px;
    padding: 15px 0px 0px 15px;
    color: var(--Gray);
}

.notification-count {
    margin-left: 40px;
    padding: 6px 13px;
    background-color: var(--Yellow);
    border-radius: 50%;
}

.profile-card,
.sidebar-footer {
    text-align: center;
}

.profile-card {
    margin: 20px 30px 10px 30px;
    padding: 10px;
    box-shadow: 0 0.8rem 1.8rem hsl(0 0% 0% / 20%);
    border-radius: 20px;
}

.profile-card img {
    width: 23%;
    border-radius: 50%;
}

.profile-title-name,
.profile-sub-title-name {
    padding: 0;
    margin: 0;
}

.profile-title-name {
    font-size: 16px;
    font-weight: bolder;
}

.profile-sub-title-name {
    font-size: 12px;
    color: var(--Gray);
    font-weight: 400;
}

.profile-card button {
    padding: 10px;
    width: 100%;
    margin: 20px 0px 10px 0px;
    border: none;
    border-radius: 10px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.profile-card button:hover {
    background-color: var(--Yellow);
}

.sidebar-footer {
    font-size: 12px;
    color: var(--Gray);
    margin: 20px 0px;
}

@media (max-width: 1024px) {
    .sidebar-section {
        overflow-y: scroll;
    }
}


