.dashboard-section {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.sidebar {
    width: 25%;
}

.dashboard {
    width: 75%;
    padding: 0px 80px;
}

.chart {
    display: flex;
    justify-content: center;
    align-items: start;
    margin: 20px 0px 25px 0px;
}

.trending-employees {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px 0px 25px 0px;
}
body {
    margin: 0px;
    padding: 0px;
    font-family: 'Poppins', sans-serif;
}

:root {
    --Yellow: #FFCA40;
    --Red: #c8161d;
    --Black: #000000;
    --Gray: #808080;
    --White: #f8f8f7;
    --scrollbarBG: #cfd8dc;
    --thumbBG: #90a4ae;
}

.col-3 {
    width: 25%;
}
